import React from "react"
import sprite from "../../img/symbol-defs.svg"

export default (props) => {
    return(<div className="home">
        <img src={props.pic} alt={props.heading} className="home__img"/>
        <svg className="home__like">
            <use xlinkHref={`${sprite}#icon-favorite`}/>
        </svg>

        <p className="home__name">{props.heading}</p>
        {props.tabs.map((item,i) => <div className={`home__${item.name}`} key={i}>
            <svg>
                <use xlinkHref={`${sprite}#${item.icon}`}/>
            </svg>
            <p>{item.text}</p>
        </div>)}

        <button className="btn home__btn">Contact Realtor</button>
    </div>)
}