import React, { Component } from "react"
import sprite from "../img/symbol-defs.svg"

export default class Sidebar extends Component {
    constructor() {
        super()
        this.state = { 
            active: false,
            pages: ["favorite_border","language","star_border","person_outline"]
        }
        this.toggleClass = this.toggleClass.bind(this)
    }
    toggleClass() {
        const currentState = this.state.active
        this.setState({ active: !currentState })
    }
    render() {
        return(<div className={`sidebar ${this.state.active ? "show-menu" : null}`}>
            <div className="sidebar__nav">
                {this.state.pages.map((item,i) => <button className="nav-btn" key={i}>
                    <svg className="nav-btn__icon">
                        <use xlinkHref={`${sprite}#icon-${item}`}/>
                    </svg>
                </button>)}
            </div>
        </div>)
    }
}