import React from "react"
import realtor1 from "../img/realtor-1.jpeg"
import realtor2 from "../img/realtor-2.jpeg"
import realtor3 from "../img/realtor-3.jpeg"

export default () => {
    const realtors = [{name: "Erik Feinman", pic: realtor1, sold: 235},{name: "Kim Brown", pic: realtor2, sold: 212},{name: "Toby Ramsey", pic: realtor3, sold: 268}]

    return(<div className="realtors">
        <p className="heading-3">Top 3 Realtors</p>
        <div className="realtors__list">
            {realtors.map((item,i) => <React.Fragment>
                <img src={item.pic} alt="realtor" className="realtors__img" key={i}/>
                <div className="realtors__details">
                    <p className="heading-4 heading-4--light">{item.name}</p>
                    <p className="realtors__sold">{`${item.sold} houses sold`}</p>
                </div>
            </React.Fragment>)}
        </div>
    </div>)
}